import slangs from "../../Slang";
import React, { Fragment } from 'react';
class Content extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            search: '',
            current: ''
        };
    }
    
    renderItem = () => {
        const item = slangs.find(item => {return item.title === this.state.current});
        console.dir(item);
        return (
            <div style={{minHeight: '300px'}}>
                <button onClick={() => this.setState({['current']: ''})}>{'Назад'}</button>
                <div style={{padding: '10px'}}>
                    <h3>{item.title}</h3>
                    <p>{item.description}</p>
                </div>
            </div>
        )
    }

    renderList = () => {
        const searchStr = this.state.search.replace( /\s+$/g, '');
        const list = this.state.search === '' ? slangs : slangs.filter(item => item.title.indexOf(searchStr) !== -1);
        return (
            <Fragment>
                <input
                    onChange={({target}) => this.setState({['search']: target.value})}
                    name={'search'}
                    value={this.state.search || ''}
                    placeholder={'Что вы хотите найти?'}
                />
                {
                    list.length > 0 
                    ? list.map(item => <div className={'slangItem'} onClick={({target}) => this.setState({['current']: item.title})}>{item.title}</div>) 
                    : <h3>{'По данному запросу ничего не найдено.'}</h3>
                }
            </Fragment>
        )
    }

    render() {
        return (
            <div className={'content'}>
                {
                    this.state.current === ''
                    ? this.renderList()
                    : this.renderItem()
                }
            </div>
        )
    }
}
export default Content;